import React, { Component } from 'react';
import './footer.css'
import { Grid, Box, Typography } from '@material-ui/core';

interface FooterState{
}

export class Footer extends Component<any,FooterState>{
    constructor(props:any){
        super(props)
        this.state ={}
    }

    render(){
        return(
            <Grid container  direction="column" justify="center" alignItems="center" className='footer'>
               <hr></hr>
                <Box mt={5} className='footerContents'>
                    <Grid container direction="row" justify="center" alignItems="center">
                    <Grid className='phoneIcon'></Grid>
                        <Box style={{display:'inline-flex'}}>
                            <Typography  variant='h5' component='h6'>1-800-123-123</Typography>
                        </Box>
                    </Grid>
                    <Grid container direction="row" justify="center" alignItems="center" style={{marginTop:'15px',minWidth:'250px'}}>
                    <Grid className='homeIcon'></Grid>
                        <Box style={{display:'inline-flex'}}>
                            <Typography   variant='h5' component='h6'>Hollywood Hills 4552</Typography>
                        </Box>
                    </Grid>
                </Box>
            </Grid>
        )
    }
}